html,body,.App{
  width: 100%;
}
.container{
  width: 80%;
  padding: 0 20px;
  margin:0 auto;
}
section{

}
section.sec_01{
  position: relative;
  /* background: url(/public/img/bg1.jpg) 50% 50% /100% auto no-repeat; */
  background: #222;
  color: #FFF;
  user-select: none;
  height: 100vh;
  min-height: 400px;
}
section.sec_01 h2{
text-shadow: 0 0 8px rgba(0,0,0,0.5);
font-weight: 100;
}
section.sec_01::before{
  content:"";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background:rgba(0,0,0,0.1);
  z-index: 1;
}
section.sec_02{
  background: #f1f1f1;
  position: relative;
  user-select:none;
}
section.sec_02::before{
  top: 280px;
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 1px;
  background: rgba(0,0,0,0.3);
}
section.sec_03{
  position: relative;
  /* background: url(/public/img/bg1.jpg) 50% 50% /100% auto no-repeat; */
  background: #222;
  color: #FFF;
  user-select: none;
}
section.sec_04{
  background: #f1f1f1;
  position: relative;
  user-select:none;
  padding: 100px 0;
}
.end_text{
  text-align: left;
  font-size: 56px;
  font-weight: 600;
}
.h100{
  height: 100%;
}

.bold{
  font-weight: 600;
}
.mt_minus20{
  margin-top: -20px;
}
.center_box{
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}
.graphic_wrap{
  display: flex;
  width: 100%;
  justify-content: space-between;
  position: relative;
  align-items: center;
}
.graphic_wrap::before{
  content: "";
  display: block;
  position: absolute;
  width: 240px;
  height: 1px;
  background: rgba(255,255,255,0.4);
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}
h2{
  text-align: left;
  font-size: 56px;
  line-height: 82px;
  font-weight: 300;
}
.sub_text{
  text-align: left;
  font-size: 21px;
  font-weight: 200;
  text-shadow: 0 0 5px rgba(0,0,0,0.5);
  line-height: 36px;
}
.about_arrow{
  position: absolute;
  bottom: 80px;
  left: 50%;
  transform: translateX(-50%);
  text-shadow: 0 0 5px rgba(0,0,0,0.5);
  font-size: 18px;
}
.about_arrow::before{
  content: "";
  display: block;
  position: absolute;
  width: 15px;
  height: 2px;
  background: #FFF;
  bottom: -30px;
  left: calc(50% - 15px);
  transform-origin: center;
  transform: rotate(60deg);
  animation: arrowA 1s 1s infinite;
}
.about_arrow::after{
  content: "";
  display: block;
  position: absolute;
  width: 15px;
  height: 2px;
  background: #FFF;
  bottom: -30px;
  left: calc(50% - 7.5px);
  transform-origin: center;
  transform: rotate(-60deg);
  animation: arrowA 1s 1s infinite;
}
.introduce_wrap{
  padding: 120px 0 20px;
  margin: 0 auto;
}
.introduce_wrap h2{
  margin-bottom: 120px;
  position: relative;
  font-size: 42px;
  font-weight: 600;
}

.introduce_wrap h3{
  text-align: left;
  font-size: 24px;
  font-weight: 500;
}
.introduce_wrap p.exp{
  margin-top: 25px;
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  margin-bottom: 25px;
}
.skills_wrap{
  margin-top: 40px;
  display: flex;
  align-items: center;
  gap: 48px;
  padding-bottom: 40px;
}
.skills_wrap h2{
  font-size: 32px;
  font-weight: 600;
}
.skill_list{
  display: flex;
  gap: 12px;  
}
.mt40{
  
}
.intro_flex{
  display: flex;
  gap: 60px;
}
.intro_flex .list{
  flex: 1;
  position: relative;
}
.intro_flex .list h3{
  font-size: 24px;
}
.intro_flex .list p.exp{
  font-size: 18px;
  line-height: 26px;
  text-align: justify;
}
.intro_flex .list::before{
  content: "";
  display: block;
  position: absolute;
  width: 9px;
  height: 9px;
  background: #111;
  border-radius: 50%;
  top: -46px;
}
.project_wrap{
  padding: 120px 0 120px;
}
.prject_list{
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  margin-top: 40px;
}
.prject_list li{
  width: calc(50% - 20px);
  background: rgba(255,255 ,255,0.2);
  color: #fff;
  padding: 40px 20px;
  border-radius: 6px;
  cursor: pointer;
  
}
.prject_list li:hover{
  transition: all 0.4s;
  background: #FFF;
  color:#222
}
.prject_list li h3{
  font-size: 28px;
  line-height: 34px;
}
.prject_list li p{
  font-size: 20px;
  padding-top: 10px;
  line-height: 28px;
}
@keyframes arrowA {
  0%,100%{
    bottom: -30px;
  }
  50%{
    bottom: -35px;
  }
}